import React, { Component } from 'react';

class Intro extends Component {
  render() {
    return (
      <section id="intro" className='intro'>
        <div className='description'>
          <h1>Academias Inteligentes perto de você!</h1>
          <p>Nosso objetivo é tornar as academias mais tecnológicas e inovadoras, proporcionando dados reais sobre sua rotina para mantê-lo focado na sua conquista pessoal.</p>
        </div>
      </section>
    );
  }
}

export default Intro;



